'use client'

import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import * as yup from 'yup'
import { useRouter } from 'next/navigation'
import toast from 'react-hot-toast'
import FormInput from '@/components/FormInput'
import { useState } from 'react'

const schema = yup.object().shape({
  firstName: yup.string().required('First name is required'),
  surname: yup.string().required('Surname is required'),
  otherNames: yup.string(),
  email: yup.string().email('Invalid email').required('Email is required'),
  penNumber: yup.string().required('PEN Number is required'),
  password: yup.string().min(6, 'Password must be at least 6 characters').required(),
  confirmPassword: yup
    .string()
    .oneOf([yup.ref('password')], 'Passwords must match')
    .required('Confirm your password'),
})

export default function RegisterPage() {
  const router = useRouter()
  const [loading, setLoading] = useState(false)

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({ resolver: yupResolver(schema) })

  const onSubmit = async (data: any) => {
    setLoading(true)
    try {
      const res = await fetch('http://localhost:5000/api/auth/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
        credentials: 'include',
      })

      const result = await res.json()
      if (!res.ok) throw new Error(result.message)
      toast.success(result.message)
      router.push('/login')
    } catch (err: any) {
      toast.error(err.message || 'Something went wrong')
    } finally {
      setLoading(false)
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="max-w-md mx-auto space-y-4 p-4">
      <FormInput label="First Name" {...register('firstName')} error={errors.firstName?.message} />
      <FormInput label="Surname" {...register('surname')} error={errors.surname?.message} />
      <FormInput label="Other Names (Optional)" {...register('otherNames')} />
      <FormInput label="Email" type="email" {...register('email')} error={errors.email?.message} />
      <FormInput label="PEN Number" {...register('penNumber')} error={errors.penNumber?.message} />
      <FormInput label="Password" type="password" {...register('password')} error={errors.password?.message} />
      <FormInput label="Confirm Password" type="password" {...register('confirmPassword')} error={errors.confirmPassword?.message} />

      <button type="submit" className="btn btn-primary w-full" disabled={loading}>
        {loading ? 'Registering...' : 'Register'}
      </button>
    </form>
  )
}
